@-webkit-keyframes colorize {
  to {
    -webkit-transform: rotate(700deg) scale(1.5);
            transform: rotate(700deg) scale(1.5);
    opacity: .5;
    /* background: linear-gradient(to bottom right, #eaf, red);
		transform: scale(2); */ } }

@keyframes colorize {
  to {
    -webkit-transform: rotate(700deg) scale(1.5);
