    opacity: .5;
    /* background: linear-gradient(to bottom right, #eaf, red);
		transform: scale(2); */ } }

.loader {
  margin: auto;
  position: fixed;
  top: calc(50vh - 25px);
  left: calc(50vw - 25px);
  width: 50px;
  height: 50px;
  background: linear-gradient(to top right, #fae, #fee);
  border-radius: 100%;
  -webkit-animation-name: colorize;
          animation-name: colorize;
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate; }
  .loader:first-of-type {
    left: calc(50vw - 135px); }
  .loader:last-of-type {
    left: calc(50vw + 85px); }

.head {
	height: 50px;
	background: linear-gradient(to right, #fae, #fed);
	display: grid;
	grid-template-columns: repeat(12, 1fr);
}

.logo {
	grid-column: 3/11;
	text-align: center;
	color: #959;
	padding: 10px 0;
	line-height: 30px;
	letter-spacing: .1em;
	font-size: 2.2em;
  font-family: 'Dancing Script', cursive;
  font-weight: bold;
  z-index: 1000;
}

.open-menu {
	grid-column: 11/-1;
	text-align: center;
}

.placeholder {
	grid-area: h;
	border-left: 1px solid white;
	text-align: center;
}

.hamburger {
  width: 40px;
  height: 30px;
  margin: auto;
  cursor: pointer;
  padding-top: 12px;
}

.hamburger-line {
  height: 4px;
  width: 100%;
  margin-bottom: 8px;
  border-radius: 2px;
  background: #959;
  z-index: 999;
}

.hamburger-line:last-of-type {
  margin-bottom: none;
}

.hamburger-line:nth-child(1) {
  --width: 40px;
  --top: 7px;
  --left: 8px;
  --rotate: 45deg;
}

.hamburger-line:nth-child(2) {
  --width: 0px;
  --top: 0px;
  --left: 0;
  --rotate: 0;
}

.hamburger-line:nth-child(3) {
  --width: 40px;
  --top: 8px;
  --left: -10px;
  --rotate: -45deg;
}

@-webkit-keyframes click {
  50% {
    width: 4px;
    -webkit-transform: rotate(0) translate(0,0);
            transform: rotate(0) translate(0,0);
  }
  51% {
    width: 4px;
    -webkit-transform: rotate(var(--rotate)) translate(var(--top),var(--left));
            transform: rotate(var(--rotate)) translate(var(--top),var(--left));
  }
  100% {
    width: var(--width);
    -webkit-transform: rotate(var(--rotate)) translate(var(--top),var(--left));
            transform: rotate(var(--rotate)) translate(var(--top),var(--left));
  }
}

@keyframes click {
  50% {
    width: 4px;
    -webkit-transform: rotate(0) translate(0,0);
            transform: rotate(0) translate(0,0);
  }
  51% {
    width: 4px;
    -webkit-transform: rotate(var(--rotate)) translate(var(--top),var(--left));
            transform: rotate(var(--rotate)) translate(var(--top),var(--left));
  }
  100% {
    width: var(--width);
    -webkit-transform: rotate(var(--rotate)) translate(var(--top),var(--left));
            transform: rotate(var(--rotate)) translate(var(--top),var(--left));
  }
}

@-webkit-keyframes expand {
  0% {
    top: -100%;
    opacity: 0;
  }
  30% {
    top: -50%;
    opacity: 0;
  }
  50% {
    top: -20%;
    opacity: 0.5;
  }
  100% {
    top: 0%;
    opacity: 1;
  }
}

@keyframes expand {
  0% {
    top: -100%;
    opacity: 0;
  }
  30% {
    top: -50%;
    opacity: 0;
  }
  50% {
    top: -20%;
    opacity: 0.5;
  }
  100% {
    top: 0%;
    opacity: 1;
  }
}

.forward-animation {
  -webkit-animation-name: click;
          animation-name: click;
  -webkit-animation-duration: .8s;
          animation-duration: .8s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

nav {
  position: relative;
  top: -100%;
  z-index: 1;
  grid-column: 3/11;
  text-align: center;
  opacity: 0;
  -webkit-animation-name: expand;
          animation-name: expand;
  -webkit-animation-duration: .8s;
          animation-duration: .8s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

nav .nav-button {
  display: inline-block;
  padding: 10px;
  border: 1px solid #fae;
  width: 25%;
  z-index: 1;
}


@-webkit-keyframes fadeIn {
  to {
    opacity: 1;
    top: 0px; } }

@keyframes fadeIn {
  to {
    opacity: 1;
    top: 0px; } }

.find-couple-form {
  width: 90%;
  height: 85vh;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }
  .find-couple-form h3 {
    font-size: 1.7em;
    margin: 16px auto; }
  .find-couple-form input[type='text'], .find-couple-form a.action-anchor {
    height: 40px;
    padding: 10px 20px;
    border-radius: 20px;
    border: 1px solid #cac;
    font-size: 1em;
    color: #737;
    margin: 16px auto; }
    .find-couple-form input[type='text']:focus, .find-couple-form a.action-anchor:focus {
      outline: none;
      border-color: #737; }
  .find-couple-form a.action-anchor {
    display: block;
    border: none;
    box-shadow: 0px 1px 3px 3px rgba(0, 0, 0, 0.05);
    line-height: 20px;
    font-size: 20px;
    text-decoration: none;
    cursor: pointer;
    background: linear-gradient(to bottom, #faf 0%, #ffdcd3 95%, #fdd 100%); }

.set-gender {
  width: 90%;
  text-align: left;
  font-size: 1.25em;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 10px;
  align-items: center; }

.set-gender-description {
  grid-column: 1/5; }

.set-gender-icon {
  grid-column: span 1;
  padding: 5px; }
  .set-gender-icon span, .set-gender-icon input[type='radio'] {
    display: block;
    height: 30px;
    width: 30px;
    border-radius: 100%;
    background: linear-gradient(to bottom, #faf 0%, #ffdcd3 95%, #fdd 100%);
    margin: auto; }

.show-candidates {
  grid-column: 1/-1; }

.form-primary {
  position: relative;
  opacity: 0.33;
  top: 30px;
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-duration: 800ms;
          animation-duration: 800ms;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  -webkit-animation-direction: normal;
          animation-direction: normal;
  -webkit-animation-delay: 500ms;
          animation-delay: 500ms; }

.form-secondary {
  position: relative;
  opacity: 0;
  top: 30px;
  display: none; }

@-webkit-keyframes slideUp {
  from {
    top: 100%;
    opacity: 0.33; }
  to {
    top: 0em;
    opacity: var(--opacity);
    font-size: var(--font-size); } }

@keyframes slideUp {
  from {
    top: 100%;
    opacity: 0.33; }
  to {
    top: 0em;
    opacity: var(--opacity);
    font-size: var(--font-size); } }

@-webkit-keyframes slideDown {
  from {
    top: -100%;
    opacity: 0.33; }
  to {
    top: 0em;
    opacity: var(--opacity); } }

@keyframes slideDown {
  from {
    top: -100%;
    opacity: 0.33; }
  to {
    top: 0em;
    opacity: var(--opacity); } }

